<template>
	<div class="sub-container">
		<h2 class="txt-center marB-50">아이디 찾기</h2>
		<div>
			<div class="input-box" v-if="!isEmailSent">
				<div class="user-input">
					<input
						type="email"
						v-model="email"
						placeholder="가입한 이메일"
						autocomplete="off"
					/>
					<span class="msg" id="check_id"></span>
				</div>
				<button class="btn full dark" id="login-btn" @click="findIdentity">
					확인
				</button>
				<LoadingSpinner :loading="loading" />
			</div>

			<div v-else class="found-container">
				<div class="found-items">
					<div class="found-item">
						<p class="body2">아이디</p>
						<p>{{ this.foundId }}</p>
					</div>
				</div>
				<button class="btn" @click="goto('/login')">로그인</button>
			</div>
		</div>
	</div>
</template>

<script>
import { findIdentity } from '@/api/user';
import LoadingSpinner from '@/components/common/LoadingSpinner';
export default {
	name: 'Login',
	components: { LoadingSpinner },
	data() {
		return {
			email: '',
			foundId: '',
			loading: false,
			isEmailSent: false,
		};
	},
	methods: {
		goto(url) {
			this.$router.push({ path: url });
		},
		async findIdentity() {
			this.loading = true;
			try {
				const { data } = await findIdentity(this.email);
				this.foundId = data.identity;
				this.isEmailSent = true;
			} catch (e) {
				console.log(e);
			} finally {
				this.loading = false;
			}
		},
	},
};
</script>

<style scoped>
.found-container {
	width: 340px;
	margin: 0 auto;
}
.found-items {
	border-top: 2px solid #e6e6e6;
	border-bottom: 2px solid #e6e6e6;
	padding: 20px 0;
	margin-bottom: 30px;
}
.found-item {
	display: flex;
	align-items: center;
	padding: 0 18px;
}
.found-item p:nth-child(1) {
	width: 60px;
	margin-right: 10px;
	color: #8c8c8c;
}
.found-container button {
	width: 100%;
}
</style>
